@use "sass:color";
$line-height: 3em;
$font-size:3em;

@for $var from 1 through 6 {
  h#{$var} {
    font-size: $font-size / $var;
    line-height: $line-height / $var;
    color: color.mix(tomato, cornflowerblue, 10px*$var);
  }
}
    $height-line: 1rem;
    $fontSize: 5rem;
  

@for $i from 1 through 6 {
  h#{$i} {
    font-size: $fontSize / $i;
    line-height: $height-line / $i;
    color: color.mix(tomato, cornflowerblue, 10%*$i);
  }
}
